html,
body {
  margin: 0;
  padding: 0;
  border: 0;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: rgb(61, 146, 201);
}
a:hover,
a:focus {
  text-decoration: underline;
}

h3 {
  font-weight: 100;
}

code,
pre {
  -webkit-hyphens: none;
      -ms-hyphens: none;
          hyphens: none;
  font-size: 12px;
}

pre {
  max-height: 30em;
  overflow: auto;
}

.padded-right {
  padding-right: 10px;
}

.img-button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.button-img {
  margin-right: 5px;
  height: 15px;
  width: 15px;
}



